.testimonial-card {
  background: #FFFFFF;
  padding: 2rem 2.5rem;
  margin: 5rem 10rem;
}
.testimonial-card h6 {
  line-height: 1.875;
}

.slick--view--testimonial-slider .slick-prev {
  width: 0;
  height: 0;
  border-top: 12.5px solid transparent;
  border-bottom: 12.5px solid transparent;
  border-right: 12.5px solid #160510;
  border-radius: 0;
  background: none;
  margin-left: 4rem;
  transition: all 0.35s ease-in-out;
  transition-duration: 0.25s;
}

.slick--view--testimonial-slider .slick-prev:hover {
  border-top: 18px solid transparent;
  border-bottom: 18px solid transparent;
  border-right: 18px solid #160510;
  margin-left: calc(4rem - 5.5px);
}

.slick--view--testimonial-slider .slick-next {
  width: 0;
  height: 0;
  border-top: 12.5px solid transparent;
  border-bottom: 12.5px solid transparent;
  border-left: 12.5px solid #160510;
  border-radius: 0;
  background: none;
  margin-right: 4rem;
  transition: all 0.35s ease-in-out;
  transition-duration: 0.25s;
}

.slick--view--testimonial-slider .slick-next:hover {
  border-top: 18px solid transparent;
  border-bottom: 18px solid transparent;
  border-left: 18px solid #160510;
  margin-right: calc(4rem - 5.5px);
}

.slick--view--testimonial-slider .slick-prev::before, .slick--view--testimonial-slider .slick-next::before {
  content: none;
}

